import React, { useState, useEffect } from 'react';
import Container from '../../Atoms/container';
import Heading from '../../Atoms/heading';
import Offer from '../../Molecules/personalLoan/offercart';
import PlApi from '../../../services/PLApi';
let api = new PlApi();


const offer = React.memo((props) => {
    const [offer, setOffer] = useState();
    const [status, setStatus] = useState(true)

    useEffect(() => {
        api.offerPage().then(res => {
            console.log('res', res)
            if (res.success) {
                setOffer(res.msg);
                setStatus(false);
            }
        })
    }, [])
    if (status) {
        return <div className="col-md-12 text-center"><p>Please wait ...</p></div>
    }
    console.log(offer, 'offer')
    return (
        <Container class="">
            <div className="col-md-12">
                <Heading title="Top Matches"></Heading>


            </div>
            <div style={{ clear: "both" }}></div>
            <Offer data={offer} />
            {/* <Offer /> */}



        </Container>
    )
})

export default offer
