import React, { useState, useEffect } from 'react';
import Container from '../../Atoms/container';
import Heading from '../../Atoms/heading';
import Paragra from '../../Atoms/paragraph';
import EmployeeType from '../../Molecules/personalLoan/Home';
import Input from '../../Molecules/input';
import Button from '../../Molecules/button';
import { numberText, emailText, nameText, emailValidator, fullname, mobileValidator, otpValidation } from '../../../helper/method';
import { Msg } from '../../../helper/plConstant';
import LoginApi from '../../../services/loginApi';
import { loginSuccess } from '../../../store/Action/loginAction';
import { useSelector, useDispatch } from 'react-redux';
import { getUserData } from '../../../store/Action/loginAction';
let api = new LoginApi()

export default function Index(props) {
    console.log(api, 'api')
    const redeState = useSelector(state => state.Emp);
    const existUserDetail = useSelector(state => state.login);
    let dispatch = useDispatch();
    const [state, setState] = useState({ email: "", mobile: "", fname: "", otp: "", isOk: false });
    const [fill, setFill] = useState(false);
    const [userExist, setUserExist] = useState(null);
    const [userOTP, setUserOTP] = useState(false);
    const [emp, setEmp] = useState();
    const [error, setError] = useState({});
    console.log(redeState, 'redeState')


    useEffect(() => {
        setEmp(redeState.emptype);
    }, [redeState.emptype])

    useEffect(() => {
        let getUser = existUserDetail.userData;
        console.log(getUser, 'getUser')
        if (getUser !== null) {
            let fname = getUser.FirstName + "" + getUser.MiddleName + " " + getUser.LastName
            setState(prevState => ({
                ...prevState,
                mobile: getUser.MobileNo,
                fname: fname,
                isOk: true

            }))
            setFill(true)


        }

    }, existUserDetail.userData)

    useEffect(() => {
        if (userExist) {
            let payload = { "emailId": state.email }
            dispatch(getUserData(payload));
            setFill(true)
            sendOtp()


        } else {

        }
    }, userExist)

    useEffect(() => () => {
        if (fill) {
            sendOtp()
        }

    }, [fill])

    // useEffect(() => {
    //     if (state.isOk) {
    //         // alert('hi')
    //     }
    // }, state.isOk)
    const sendOtp = () => {
        // alert()
        let { email, mobile } = state;
        let payload = {
            "otpType": userExist ? "signin" : "signup",
            "emailId": email,
            "mobileNo": mobile.toString()
        };
        let senddata = api.sendOtp(payload).then(res => {
            console.log(res, 'ressend')
            if (res.status) {
                setUserOTP(true)
            } else {
                //setUserOTP(true)
            }

        })

    }




    const changeHandler = (name, value) => {
        let allState = state;
        if (name === "mobile" || name === "otp") {
            if (value === "" || numberText(value)) {
                allState[name] = value;
            }
        } else if (name === 'fname') {
            if (value === "" || nameText(value)) {
                allState[name] = value;
            }
        } else {
            allState[name] = value;
        }
        setState(prevState => ({
            ...prevState,
            ...allState
        }))
    }
    const validate = (type, value) => {
        let errormsg = { ...error };
        if (type === "email") {
            let emailCheck = emailValidator(value);
            if (!emailCheck.res) {
                errormsg['emailError'] = emailCheck.msg;
            } else {
                errormsg['emailError'] = emailCheck.msg;
                let payload = {
                    "emailId": value
                };
                api.userExist(payload).then(data => {
                    if (data.status === "success") {
                        setUserExist(true)
                    } else {
                        setUserExist(false)
                    }
                    console.log(data, 'then')
                });
            }

            setError({ ...errormsg })
        }
        if (type === "mobile") {
            let mobileCheck = mobileValidator(value);
            if (!mobileCheck.res) {
                errormsg['mobileError'] = mobileCheck.msg;
            } else {
                errormsg['mobileError'] = mobileCheck.msg;
                let emailCheck = emailValidator(state.email);

                let nameCheck = fullname(state.fname);

                if (!emailCheck.res) {
                    errormsg['emailError'] = emailCheck.msg;
                } else if (!nameCheck.res) {
                    errormsg['fnameError'] = nameCheck.msg;
                } else {
                    errormsg['emailError'] = "";
                    errormsg['mobileError'] = "";
                    errormsg['fnameError'] = "";
                    sendOtp()
                }
                setError({ ...errormsg })

            }
            setError({ ...errormsg })
        }
        if (type === "otp") {
            let otpCheck = otpValidation(value);
            if (!otpCheck.res) {
                errormsg['otpError'] = otpCheck.msg;
            } else {
                errormsg['otpError'] = otpCheck.msg;
            }
            setError({ ...errormsg })
        }
        if (type === "fname") {
            let nameCheck = fullname(value);
            if (!nameCheck.res) {
                errormsg['fnameError'] = nameCheck.msg;
            } else {
                errormsg['fnameError'] = nameCheck.msg
            }
            setError({ ...errormsg })
        }


    }
    const validFun = () => {
        let errormsg = { ...error };
        let { email, mobile, fname, otp } = state;
        let formCheck = false;
        let emailCheck = emailValidator(email);
        let mobileCheck = mobileValidator(mobile);
        let otpCheck = otpValidation(otp);
        let nameCheck = fullname(fname);

        if (!emailCheck.res) {
            errormsg['emailError'] = emailCheck.msg;
        } else if (!mobileCheck.res) {
            errormsg['mobileError'] = mobileCheck.msg;
        } else if (!nameCheck.res) {
            errormsg['fnameError'] = nameCheck.msg;
        } else if (!otpCheck.res) {
            errormsg['otpError'] = otpCheck.msg;
        } else {
            formCheck = true
        }
        setError({ ...errormsg })
        return formCheck
    }

    const submit = () => {
        let check = validFun();
        if (check) {
            let url = window.location.href;
            let { email, fname, mobile, otp } = state;
            let layout = {
                "emailId": email,
                "mobileNo": mobile,
                "fullName": fname,
                "otp": otp,
                "pageUrl": url
            }
            api.fastCheck(layout).then(res => {
                console.log(res, 'sujeetOK')
                // let data = { "status": "success", "data": { "uid": 40721 }, "message": "Signed In." };
                let uuid = res.data.uid
                if (res.status === "success") {
                    let reduxData = {
                        uuid: uuid,
                        email: email,
                        mobile: mobile,
                        fullName: fname



                    }
                    dispatch(loginSuccess(reduxData));
                    props.history.push("/pl/BasicDetail");
                }
            })
        }
    }

    //console.log(allState, 'allState')
    console.log(fill, 'fill')
    return (
        <Container class="container-con padd_con">
            <div className="col-md-12">
                <Heading title="Personal Loans"></Heading>
                <Paragra para="Connecting you to the best deals available!" />

            </div>
            <div className="col-md-12">
                <EmployeeType />
            </div>
            <div className="col-md-12 padd0">
                <div className="col-md-3">
                    <Input
                        placeholder="example@mail.com"
                        label="Enter your email ID"
                        onChange={changeHandler}
                        type="email"
                        name="email"
                        maxLength="65"
                        errorMsg={error.emailError}
                        onBlur={() => validate('email', state.email)}
                        value={state.email}

                    />
                </div>
                {userExist !== null ?
                    <React.Fragment>
                        <div className="col-md-3">
                            <Input
                                placeholder="Enter full name"
                                label="Enter Full Name"
                                type="text"
                                name="fname"
                                maxLength="70"
                                disabled={userExist ? true : false}
                                value={state.fname}
                                errorMsg={error.fnameError}
                                onBlur={() => validate('fname', state.fname)}
                                onChange={changeHandler}

                            />
                        </div>
                        <div className="col-md-3">
                            <Input
                                placeholder="Enter mobile number"
                                label="Enter mobile number"
                                type="text"
                                name="mobile"
                                maxLength="10"
                                disabled={userExist ? true : false}
                                value={state.mobile}
                                errorMsg={error.mobileError}
                                onBlur={() => validate('mobile', state.mobile)}
                                onChange={changeHandler}

                            />
                        </div>
                        <div className="col-md-3">
                            <Input
                                placeholder="Enter OTP"
                                label="Enter OTP"
                                type="text"
                                name="otp"
                                maxLength="6"
                                errorMsg={error.otpError}
                                value={state.otp}
                                onBlur={() => validate('otp', state.otp)}
                                onChange={changeHandler}

                            />
                        </div>

                    </React.Fragment>
                    : null}

                {/* {userOTP ?
                     : null} */}
            </div>
            <div className="col-md-12">
                <small class="form-text" style={{ fontSize: "12px", paddingTop: "0px" }}>By clicking on "Submit" you hereby accept the <a href="https://www.adityabirlacapital.com/home/terms-and-conditions" target="_blank" rel="noopener noreferrer">Terms &amp; Conditions</a> and <a href="https://www.adityabirlacapital.com/home/privacy-policy" target="_blank" rel="noopener noreferrer">Privacy Policy</a> for Aditya Birla Finance Limited.</small>
            </div>
            <div className="col-md-12" style={{ marginTop: "15px" }}>

                <Button name="Submit" click={submit} />
            </div>

        </Container>
    )
}
