import React, { useState } from 'react';
import Aux from '../../Atoms/children';
import Input from '../../Molecules/input';
import Button from '../../Molecules/button';
import { numberText, pincodeVali, addressVali } from '../../../helper/method';
const AddressDetail = (props => {
    const [state, setState] = useState({ pincode: "", city: "", address1: "", address2: "", address3: '' })
    const [pinStatus, setPinStatus] = useState(true);
    const [error, setError] = useState({});
    const validate = (name, value) => {
        let errorMsg = { ...error };
        let type = name;
        if (type === "pincode") {
            let pinCheck = pincodeVali(value);
            if (!pinCheck.res) {
                errorMsg['pincodeError'] = pinCheck.msg
            } else {
                //errorMsg['pincodeError'] = pinCheck.msg
                let payload = {
                    "pincode": value
                }
                props.pincodeFn(payload).then(res => {
                    //console.log(res, 'pincode')
                    if (res.success) {
                        errorMsg['pincodeError'] = "";
                        let data = res.response[0];
                        setState(prevState => ({
                            ...prevState,
                            city: data.cityname,
                            //state: data.statename,
                        }))
                        setPinStatus(false)
                    } else {
                        errorMsg['pincodeError'] = res.response;
                    }
                    setError(errorMsg)
                })
            }
            setError(errorMsg)
        }
        if (type === "address1") {
            let pinCheck = addressVali(value);
            if (!pinCheck.res) {
                errorMsg['address1Error'] = pinCheck.msg
            } else {
                errorMsg['address1Error'] = pinCheck.msg
            }
            setError(errorMsg)
        }
        if (type === "address2") {
            let pinCheck = addressVali(value);
            if (!pinCheck.res) {
                errorMsg['address2Error'] = pinCheck.msg
            } else {
                errorMsg['address2Error'] = pinCheck.msg
            }
            setError(errorMsg)
        }
        if (type === "address3") {
            if (value !== "") {
                let pinCheck = addressVali(value);
                if (!pinCheck.res) {
                    errorMsg['address3Error'] = pinCheck.msg
                } else {
                    errorMsg['address3Error'] = pinCheck.msg
                }

            } else {
                errorMsg['address3Error'] = ""
            }
            setError(errorMsg)

        }


    }
    const changeHandler = (name, value) => {
        let allState = { ...state };
        if (name === "pincode") {
            if (value === "" || numberText(value)) {
                allState[name] = value
            }
        } else {
            allState[name] = value
        }
        setState(allState)
    }
    let submitValidation = () => {
        let { pincode, city, address1, address2, address3 } = state;
        let isValid = false;
        let errorMsg = { ...error };
        let pinCheck = pincodeVali(pincode);
        if (!pinCheck.res) {
            errorMsg['pincodeError'] = pinCheck.msg
        } else if (!addressVali(address1).res) {
            errorMsg['address1Error'] = addressVali(address1).msg
        } else if (!addressVali(address2).res) {
            errorMsg['address2Error'] = addressVali(address2).msg
        } else if (address3 !== "") {
            if (!addressVali(address3).res) {
                errorMsg['address3Error'] = addressVali(address3).msg
            } else {
                isValid = true
                errorMsg['address3Error'] = addressVali(address3).msg
            }
        } else {
            errorMsg['pincodeError'] = "";
            errorMsg['address1Error'] = "";
            errorMsg['address2Error'] = "";
            errorMsg['address3Error'] = "";
            isValid = true
        }
        setError(errorMsg);
        return isValid

    }
    const submit = (data) => {
        let check = submitValidation();
        console.log(check, 'check')
        if (check) {
            let { pincode, city, address1, address2, address3 } = state;
            let layout = {
                "addLine1": address1,
                "addLine2": address2,
                "addLine3": address3,
                "city": city,
                "pincode": pincode,
            }
            props.addressClick(layout)
        }

    }
    return (
        <Aux>
            <div className="col-md-4">
                <Input

                    label="Pincode"

                    placeholder="Enter pincode"
                    name="pincode"
                    maxLength={6}
                    value={state.pincode}
                    onBlur={() => validate('pincode', state.pincode)}
                    onChange={changeHandler}
                    errorMsg={error.pincodeError}

                />
            </div>
            <div className="col-md-4">
                <Input
                    name="city"
                    label="City"
                    placeholder="Enter city"
                    value={state.city}
                    disabled={true}
                    onBlur={() => validate('city', state.city)}
                    onChange={changeHandler}
                    errorMsg={error.cityError}

                />
            </div>
            <div className="col-md-4">
                <Input
                    name="address1"
                    label="Address line 1"
                    placeholder="Enter address"
                    value={state.address1}
                    onBlur={() => validate('address1', state.address1)}
                    onChange={changeHandler}
                    errorMsg={error.address1Error}

                />
            </div>
            <div className="col-md-4">
                <Input
                    name="address2"
                    label="Address line 1"
                    placeholder="Enter address"
                    value={state.address2}
                    onBlur={() => validate('address2', state.address2)}
                    onChange={changeHandler}
                    errorMsg={error.address2Error}

                />
            </div>
            <div className="col-md-4">
                <Input
                    name="address3"
                    label="Address line 3"
                    placeholder="Enter address"
                    value={state.address3}
                    onBlur={() => validate('address3', state.address3)}
                    onChange={changeHandler}
                    errorMsg={error.address3Error}

                />
            </div>
            <div className="col-md-12 text-right" style={{ marginTop: "15px" }}>
                <Button name="Submit" disabled={pinStatus} click={submit} />
            </div>
        </Aux>
    )
})

export default AddressDetail


