import React, { useState } from 'react';
import Aux from '../../Atoms/children';
import Select from '../../Molecules/select';
import Input from '../../Molecules/input';
import Button from '../../Molecules/button';
import { useSelector } from 'react-redux';
import { numberText, incomevali, loanvali, dropvali, emivali, comNameValue } from '../../../helper/method';

const EmpDetail = React.memo((props) => {
    const getRedux = useSelector(state => state);
    console.log(getRedux, 'getRedux')
    let gender = [
        { label: "male", value: "male" },
        { label: "Femal", value: "Femal" },
        { label: "Other", value: "Other" }
    ]
    const [state, setState] = useState({ Gender: "", Mincome: "", Purpose: "", Emi: "", CompanyName: "", ProfessionType: "", WorkEx: "", city: props.city, ResidencyType: "", SalaryRecived: "", LoanAmt: "" });
    const [error, setError] = useState({});
    const changeHandler = (name, value) => {
        console.log(name, "===change", value);
        let allState = { ...state };
        if (name === 'Mincome' || name === "Emi" || name === "LoanAmt") {
            if (value === "" || numberText(value)) {
                allState[name] = value;
            }
        } else {
            allState[name] = value;
        }

        setState(allState)
    }
    const validate = (name, value) => {
        let type = name;
        let errorMsg = { ...error };
        console.log(name, 'name');
        if (type === "Gender") {
            let genderV = dropvali(value);
            if (!genderV.res) {
                errorMsg['GenderError'] = genderV.msg;
            } else {
                errorMsg['GenderError'] = genderV.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "Mincome") {
            let icheck = incomevali(value);
            if (!icheck.res) {
                errorMsg['MincomeError'] = icheck.msg;
            } else {
                errorMsg['MincomeError'] = icheck.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "Purpose") {
            let purposeCheck = dropvali(value);
            if (!purposeCheck.res) {
                errorMsg['PurposeError'] = purposeCheck.msg;
            } else {
                errorMsg['PurposeError'] = purposeCheck.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "Emi") {
            let emiCheck = emivali(value);
            if (!emiCheck.res) {
                errorMsg['EmiError'] = emiCheck.msg;
            } else {
                errorMsg['EmiError'] = emiCheck.msg;;
            }
            setError({ ...errorMsg });
        }
        if (type === "CompanyName") {
            let check = comNameValue(value);
            if (!check.res) {
                errorMsg['CompanyNameError'] = check.msg;
            } else {
                errorMsg['CompanyNameError'] = check.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "ProfessionType") {
            let checkVal = dropvali(value);
            if (!checkVal.res) {
                errorMsg['ProfessionTypeError'] = checkVal.msg;
            } else {
                errorMsg['ProfessionTypeError'] = checkVal.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "WorkEx") {
            let checkVal = dropvali(value);
            if (!checkVal.res) {
                errorMsg['WorkExError'] = checkVal.msg;
            } else {
                errorMsg['WorkExError'] = checkVal.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "ResidencyType") {
            let checkVal = dropvali(value);
            if (!checkVal.res) {
                errorMsg['ResidencyTypeError'] = checkVal.msg;
            } else {
                errorMsg['ResidencyTypeError'] = checkVal.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "SalaryRecived") {
            let checkVal = dropvali(value);
            if (!checkVal.res) {
                errorMsg['SalaryRecivedError'] = checkVal.msg;
            } else {
                errorMsg['SalaryRecivedError'] = checkVal.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "LoanAmt") {
            let amtcheck = loanvali(value);
            if (!amtcheck.res) {
                errorMsg['LoanAmtError'] = amtcheck.msg;
            } else {
                errorMsg['LoanAmtError'] = amtcheck.msg;
            }
            setError({ ...errorMsg });
        }

    }

    const submitValidation = () => {
        let { Gender, Mincome, Purpose, Emi, CompanyName, ProfessionType, WorkEx, city, ResidencyType, SalaryRecived, LoanAmt } = state;
        let errorMsg = { ...error };
        let formValid = false;
        let genderCheck = dropvali(Gender);

        if (!genderCheck.res) {
            errorMsg['GenderError'] = genderCheck.msg;
        } else if (!incomevali(Mincome).res) {
            errorMsg['MincomeError'] = incomevali(Mincome).msg;
        } else if (!dropvali(Purpose).res) {
            errorMsg['PurposeError'] = dropvali(Purpose).msg;
        } else if (!emivali(Emi).res) {
            errorMsg['EmiError'] = emivali(Emi).msg;
        } else if (!comNameValue(CompanyName).res) {
            errorMsg['CompanyNameError'] = comNameValue(CompanyName).msg;
        } else if (!dropvali(ProfessionType).res) {
            errorMsg['ProfessionTypeError'] = dropvali(ProfessionType).msg;
        } else if (!dropvali(WorkEx).res) {
            errorMsg['WorkExError'] = dropvali(WorkEx).msg;
        } else if (!dropvali(ResidencyType).res) {
            errorMsg['ResidencyTypeError'] = dropvali(ResidencyType).msg;
        }
        else if (!dropvali(SalaryRecived).res) {
            errorMsg['SalaryRecivedError'] = dropvali(SalaryRecived).msg;
        }
        else if (!loanvali(LoanAmt).res) {
            errorMsg['LoanAmtError'] = loanvali(LoanAmt).msg;
        } else {
            errorMsg['GenderError'] = "";
            formValid = true
        }

        setError({ ...errorMsg });
        return formValid

    }
    const submit = () => {
        let { Gender, Mincome, Purpose, Emi, CompanyName, ProfessionType, WorkEx, city, ResidencyType, SalaryRecived, LoanAmt } = state;
        let checkF = submitValidation();
        if (checkF) {
            let data = {
                "udid": getRedux.login.uuid.uuid,
                "leadid": getRedux.Emp.leadID,
                "gender": Gender,
                "monthlyIncome": Mincome,
                "grossAnnualInc": "0",
                "loanPurpose": Purpose,
                "totalEmi": Emi,
                "employerName": CompanyName,
                "currWorkExp": "0",
                "totalWorkExp": WorkEx,
                "resCity": city,
                "resType": ResidencyType,
                "salaryBank": SalaryRecived,
                "moratoriumAvail": false,
                "profType": "",
                "currProfExp": "0",
                "savingAcc": "",
                "emptype": getRedux.Emp.emptype,
                "reqLoanAmt": LoanAmt
            }
            props.finalSubmit(data)
        }



    }
    let bankname = props.emp.bankName;
    let { LoanPurpose, ResiType, Profession, WorkExp, Gender, IncomeRange } = props.dropdown
    return (
        <Aux>
            <div className="col-md-4">
                <Select
                    label="Select Gender"
                    name="Gender"
                    options={Gender}
                    value={state.Gender}
                    onBlur={() => validate('Gender', state.Gender)}
                    onChange={changeHandler}
                    errorMsg={error.GenderError}
                />
            </div>

            <div className="col-md-4">
                <Input
                    label="Your net monthly income"
                    options={IncomeRange}
                    name="Mincome"
                    placeholder="Enter monthly income"
                    value={state.Mincome}
                    onChange={changeHandler}
                    errorMsg={error.MincomeError}
                    onBlur={() => validate('Mincome', state.Mincome)}
                />
            </div>
            <div className="col-md-4">
                <Select
                    label="What is the purpose of your loan"
                    name="Purpose"
                    options={LoanPurpose}
                    value={state.Purpose}
                    onChange={changeHandler}
                    errorMsg={error.PurposeError}
                    onBlur={() => validate('Purpose', state.Purpose)}
                />
            </div>
            <div className="col-md-4">
                <Input
                    placeholder="Total EMI you Pay currently"
                    label="Total EMI you pay currently"
                    name="Emi"
                    value={state.Emi}
                    onChange={changeHandler}
                    errorMsg={error.EmiError}
                    onBlur={() => validate('Emi', state.Emi)}
                />
            </div>
            <div className="col-md-4">
                <Input
                    placeholder="Enter your company name"
                    label="Enter Company name"
                    name="CompanyName"
                    value={state.CompanyName}
                    onChange={changeHandler}
                    errorMsg={error.CompanyNameError}
                    onBlur={() => validate('CompanyName', state.Emi)}
                />
            </div>
            <div className="col-md-4">
                <Select
                    placeholder="Your tenture is the current company"
                    label="Select profession type"
                    name="ProfessionType"
                    value={state.ProfessionType}
                    onChange={changeHandler}
                    options={Profession}
                    errorMsg={error.ProfessionTypeError}
                    onBlur={() => validate('ProfessionType', state.ProfessionType)}
                />
            </div>
            <div className="col-md-4">
                <Select
                    placeholder="Select total work experience"
                    label="Enter total work experience"
                    name="WorkEx"
                    options={WorkExp}
                    value={state.WorkEx}
                    onChange={changeHandler}
                    errorMsg={error.WorkExError}
                    onBlur={() => validate('WorkEx', state.WorkEx)}
                />
            </div>
            <div className="col-md-4">
                <Input
                    placeholder="Select current city"
                    label="Your current city of residency"
                    name="city"
                    value={props.city}
                    disabled={true}
                    options={gender}
                    errorMsg={error.cityError}
                    onBlur={() => validate('city', state.city)}
                />
            </div>
            <div className="col-md-4">
                <Select
                    placeholder="select Residency type"
                    label="Residence Type"
                    name="ResidencyType"
                    options={ResiType}
                    value={state.ResidencyType}
                    onChange={changeHandler}
                    errorMsg={error.ResidencyTypeError}
                    onBlur={() => validate('ResidencyType', state.ResidencyType)}
                />
            </div>
            <div className="col-md-4">
                <Select
                    placeholder="Select primary bank"
                    label="Salary recieved in"
                    name="SalaryRecived"
                    value={state.SalaryRecived}
                    onChange={changeHandler}
                    options={bankname}
                    onBlur={() => validate('SalaryRecived', state.SalaryRecived)}
                    errorMsg={error.SalaryRecivedError}
                />
            </div>

            <div className="col-md-4">
                <Input
                    placeholder="Enter loan amount required"
                    label="Enter loan amount required"
                    name="LoanAmt"
                    value={state.LoanAmt}
                    onChange={changeHandler}
                    errorMsg={error.LoanAmtError}
                    onBlur={() => validate('LoanAmt', state.LoanAmt)}

                />
            </div>
            <div className="col-md-12" style={{ marginTop: "15px" }}>
                <Button name="Show Offers" click={submit} />
            </div>
        </Aux>

    )
})

export default EmpDetail