import * as type from '../Action/type';
const initialState = {
    userData: null,
    userDataStatus: false,
    uuid: null
}

export default function loginReducer(state = initialState, action) {
    switch (action.type) {
        case type.GET_USER_DATA:
            return {
                ...state,
                userDataStatus: true

            }
        case type.GET_USER_SUCCESS:
            console.log(action, 'sujeet')
            return {
                ...state,
                userDataStatus: false,
                userData: action.payload
            }
        case type.GET_USER_FAIL:
            console.log(action, 'ac')
            return {
                ...state,
                userDataStatus: false,
                userData: null
            }
        case type.LOGIN_SUCCESS:
            return {
                ...state,
                uuid: action.payload

            }
        default:
            return state

    }
}