import React, { useState } from 'react';
import Container from '../../Atoms/container';
import Heading from '../../Atoms/heading';
import Input from '../../Molecules/input';
import DOB from '../../Molecules/DOB';
import Button from '../../Molecules/button';
import SelfEmpDetail from './selfempDetail';
import { nameText, numberText, validateDOB, nameValid, pincodeVali, panValidation } from '../../../helper/method';
import { useSelector, useDispatch } from 'react-redux';
import EmpDetail from './empdetail';
import { getLeadID } from '../../../store/Action/PL/employeeAction';
import PlApi from '../../../services/PLApi';
let api = new PlApi();

function BasicDetail(props) {
    const reduxStore = useSelector(state => state.Emp);
    const login = useSelector(state => state.login)
    const dispatch = useDispatch();
    const [city, setCity] = useState('');
    let [basic, setBasic] = useState({ Fname: "", Mname: "", Lname: "", DOB: "", pincode: "", pan: "" })
    const [validCheck, setValidCheck] = useState(false)
    const [pincodeStatus, setPincodeStatus] = useState(false);
    const [error, setError] = useState({});
    let { emp } = props;
    let { masterData } = emp;
    const changeHandler = (name, value) => {
        let allBasic = { ...basic };
        if (name === "Fname" || name === "Mname" || name === "Lname") {
            if (value === "" || nameText(value))
                allBasic[name] = value
        } else if (name === "pincode") {
            if (value === "" || numberText(value)) {
                allBasic[name] = value
            }

        } else if (name === "pan") {
            allBasic[name] = value.toUpperCase()
        } else {
            allBasic[name] = value
        }
        setBasic(allBasic)


    }
    const validate = (name, value) => {
        let type = name;
        let errorMsg = { ...error }
        if (type === "Fname") {
            let nameCheck = nameValid(value)
            if (!nameCheck.res) {
                errorMsg['FnameError'] = nameCheck.msg
            } else {
                errorMsg['FnameError'] = nameCheck.msg
            }
            setError({ ...errorMsg })
        }
        if (type === "Mname") {
            if (value !== "") {
                let nameCheck = nameValid(value)
                if (!nameCheck.res) {
                    errorMsg['MnameError'] = nameCheck.msg
                } else {
                    errorMsg['MnameError'] = nameCheck.msg
                }
                setError({ ...errorMsg })
            }

        }
        if (type === "Lname") {
            let nameCheck = nameValid(value)
            if (!nameCheck.res) {
                errorMsg['LnameError'] = nameCheck.msg
            } else {
                errorMsg['LnameError'] = nameCheck.msg
            }
            setError({ ...errorMsg })
        }
        if (type === "DOB") {
            let checkDOB = validateDOB(value);
            console.log(checkDOB, 'checkDOB')
            if (!checkDOB.res) {
                errorMsg['DOBError'] = checkDOB.msg
            } else {
                if (checkDOB.age < 18) {
                    errorMsg['DOBError'] = 'Minimum age should be 18 years'
                } else {
                    errorMsg['DOBError'] = ""
                }

            }
            setError({ ...errorMsg })
        }
        if (type === "pincode") {
            let check = pincodeVali(value);
            if (!check.res) {
                errorMsg['pincodeError'] = check.msg
            } else {
                let data = {
                    "pincode": basic.pincode
                }
                api.getCityState(data).then(res => {
                    // let data = { "success": true, "response": [{ "pincode": 400084, "cityid": 429, "stateid": 15, "cityname": "Mumbai", "statename": "Maharashtra" }] };

                    if (res.success) {
                        let city = res.response[0].cityname
                        setPincodeStatus(true)
                        setCity(city)
                        errorMsg['pincodeError'] = ""
                    } else {
                        setPincodeStatus(false)
                        errorMsg['pincodeError'] = 'Enter valid pincode'
                    }

                    setError({ ...errorMsg })
                })
                //errorMsg['pincodeError'] = check.msg
            }
            setError({ ...errorMsg })
        }
        if (type === "pan") {
            //alert()
            let check = panValidation(value);
            if (!check.res) {
                errorMsg['panError'] = check.msg
            } else {
                errorMsg['panError'] = check.msg
            }
            setError({ ...errorMsg })
        }
    }
    const getCityState = () => {

    }
    const validation = () => {
        let errorMsg = { ...error }
        let { Fname, Mname, Lname, DOB, pincode, pan } = basic;
        let isValid = false;
        let fCheck = nameValid(Fname);
        let mCheck = nameValid(Mname);
        let lCheck = nameValid(Lname);
        let checkDOB = validateDOB(DOB);
        let pincodeCheck = pincodeVali(pincode);
        let panCheck = panValidation(pan);
        if (!fCheck.res) {
            errorMsg['FnameError'] = fCheck.msg
        } else if (!lCheck.res) {
            errorMsg['LnameError'] = lCheck.msg
        } else if (!checkDOB.res) {
            errorMsg['DOBError'] = checkDOB.msg
        } else if (checkDOB.res) {
            if (checkDOB.age < 18) {
                errorMsg['DOBError'] = 'Minimum age should be 18 years';


            } else {
                errorMsg['DOBError'] = "";
                isValid = true;
            }
        } else if (!pincodeCheck.res) {
            errorMsg['pincodeError'] = pincodeCheck.msg;
        } else if (!panCheck.res) {
            errorMsg['panError'] = panCheck.msg;
        } else {
            errorMsg['FnameError'] = "";
            errorMsg['LnameError'] = "";
            errorMsg['DOBError'] = "";
            errorMsg['pincodeError'] = "";
            errorMsg['panError'] = "";
            isValid = true


        }
        setError({ ...errorMsg })
        return isValid
    }

    const panCheck = () => {
        let check = validation();
        console.log(check, 'check')
        if (check) {
            //alert('hi')
            let url = window.location.href;
            let { Fname, Mname, Lname, DOB, pincode, pan } = basic;
            let payload = {
                "empType": reduxStore.emptype,
                "fname": Fname,
                "mname": Mname,
                "lname": Lname,
                "dob": DOB,
                "pincode": pincode,
                "pan": pan,
                "pageurl": url,
                "udid": login.uuid.uuid,
                "emailid": login.uuid.email,
                "mobno": login.uuid.mobile.toString()
            }
            api.personalDetails(payload).then(res => {
                // let data = { "success": true, "msg": "Successfully saved", "data": { "umid": 2, "leadid": 12 } };
                if (res.success) {
                    let leadid = res.data.leadid
                    setValidCheck(true)
                    dispatch(getLeadID(leadid))
                }
                // console.log('personalDetail', res)
            })
            //setValidCheck(true)
        }

    }
    const finalSubmit = (data) => {
        console.log('finalSubmit', data)
        api.basicForm(data).then(res => {
            let checkApi = res;
            if (res.success) {
                props.history.push("/pl/Offers");
            }
            console.log(res, 'basicCall')
        })
    }
    console.log(basic, 'value')
    return (
        <Container class="container-con padd_con">
            <div className="col-md-12">
                <Heading title="Some Basic Details About You"></Heading>


            </div>

            <div className="col-md-12 padd0">
                <div className="col-md-4">
                    <Input
                        placeholder="Enter first name"
                        label="First Name"
                        type="text"
                        name="Fname"
                        onChange={changeHandler}
                        onBlur={() => validate('Fname', basic.Fname)}
                        value={basic.Fname}
                        disabled={validCheck ? true : false}
                        errorMsg={error.FnameError}


                    />
                </div>
                <div className="col-md-4">
                    <Input
                        placeholder="Enter middle number"
                        label="Middle Name"
                        type="text"
                        name="Mname"
                        disabled={validCheck ? true : false}
                        onChange={changeHandler}
                        value={basic.Mname}
                        onBlur={() => validate('Mname', basic.Mname)}
                        errorMsg={error.MnameError}

                    />
                </div>
                <div className="col-md-4">
                    <Input
                        placeholder="Enter last name"
                        label="Last Name"
                        type="text"
                        name="Lname"
                        onChange={changeHandler}
                        disabled={validCheck ? true : false}
                        value={basic.Lname}
                        onBlur={() => validate('Lname', basic.Lname)}
                        errorMsg={error.LnameError}

                    />
                </div>
                <div className="col-md-4">
                    <DOB
                        placeholder="Enter DOB"
                        label="Date of Birth"
                        type="text"
                        onChange={changeHandler}
                        name="DOB"
                        value={basic.DOB}
                        disabled={validCheck ? true : false}
                        onBlur={() => validate('DOB', basic.DOB)}
                        errorMsg={error.DOBError}


                    />
                </div>
                <div className="col-md-4">
                    <Input
                        placeholder="Enter pincode"
                        label="Pincode"
                        type="text"
                        name="pincode"
                        onChange={changeHandler}
                        value={basic.pincode}
                        disabled={validCheck ? true : false}
                        onBlur={() => validate('pincode', basic.pincode)}
                        errorMsg={error.pincodeError}

                    />
                </div>
                <div className="col-md-4">
                    <Input
                        placeholder="Enter PAN No"
                        label="What's your PAN number?"
                        type="text"
                        name="pan"
                        disabled={validCheck ? true : false}
                        onChange={changeHandler}
                        value={basic.pan}
                        onBlur={() => validate('pan', basic.pan)}
                        errorMsg={error.panError}

                    />
                </div>
            </div>
            {/* <div style={{ clear: "both", width: '100%', border: '1px solid red' }}></div> */}
            {validCheck ? null :
                <div className="col-md-12 text-right" style={{ marginTop: "15px" }}>
                    <Button name="Validate PAN" click={panCheck} disabled={pincodeStatus ? false : true} />
                </div>}
            {validCheck ?
                <React.Fragment>
                    {masterData !== "" ?
                        <React.Fragment>
                            {reduxStore.emptype === "SA" ?
                                <EmpDetail dropdown={masterData} {...props} city={city} finalSubmit={finalSubmit} /> : null
                            }
                            {reduxStore.emptype === "SEP" ?
                                <SelfEmpDetail dropdown={masterData} {...props} city={city} finalSubmit={finalSubmit} /> : null
                            }
                        </React.Fragment>
                        : null}

                    {/* <EmpDetail /> */}

                </React.Fragment> : null
            }


        </Container>
    )
}

export default React.memo(BasicDetail)
