import React, { memo, useState, useEffect } from 'react';
import Aux from '../../../Atoms/children';
import Input from '../../../Molecules/input';
import Button from '../../../Molecules/button';
import { numberText, nameText, nameValid, mobileValidator } from '../../../../helper/method';


const PersonalRef = memo(props => {
    const [error, setError] = useState({});
    const [state, setState] = useState({ fname: "", lname: "", mobile: "" });
    const changeHandler = (name, value) => {
        let allstate = { ...state };
        if (name === 'fname' || name === "lname") {
            if (value === "" || nameText(value)) {
                allstate[name] = value
            }
        } else if (name === "mobile") {
            if (value === "" || numberText(value)) {
                allstate[name] = value
            }

        } else {
            allstate[name] = value
        }
        setState(allstate)
    }
    const validate = (name, value) => {
        let type = name;
        let errorMsg = { ...error };
        let { fname, lname, mobile } = state;
        if (type === "fname") {
            let check = nameValid(value);
            if (!check.res) {
                errorMsg['fnameError'] = check.msg
            } else {
                errorMsg['fnameError'] = check.msg
            }
            setError(errorMsg)
        }
        if (type === "lname") {

            let check = nameValid(value);
            if (!check.res) {
                errorMsg['lnameError'] = check.msg
            } else {
                errorMsg['lnameError'] = check.msg
            }
            setError(errorMsg)

        }
        if (type === "mobile") {

            let check = mobileValidator(value);
            if (!check.res) {
                errorMsg['mobileError'] = check.msg
            } else {
                errorMsg['mobileError'] = check.msg
            }
            setError(errorMsg)

        }

    }

    const submitValidation = () => {
        let { fname, lname, mobile } = state;
        let errorMsg = { ...error };
        let isValid = false;
        if (!nameValid(fname).res) {
            errorMsg['fnameError'] = nameValid(fname).msg
        } else if (!nameValid(lname).res) {
            errorMsg['lnameError'] = nameValid(lname).msg
        } else if (!mobileValidator(mobile).res) {
            errorMsg['mobileError'] = mobileValidator(mobile).msg
        } else {
            errorMsg['fnameError'] = "";
            errorMsg['lnameError'] = "";
            errorMsg['mobileError'] = ""
            isValid = true
        }
        setError(errorMsg);
        return isValid
    }

    const submit = () => {
        let check = submitValidation();

        if (check) {
            let { fname, lname, mobile } = state;
            let paylaod = {
                "firstName": fname,
                "lastName": lname,
                "mobno": mobile,

            }
            //alert()
            props.refClick(paylaod)
        }

    }

    return (
        <Aux>
            <div className="col-md-4">
                <Input
                    name="fname"
                    label="First Name"
                    placeholder="Enter first name"
                    value={state.fname}
                    maxLength="75"
                    onChange={changeHandler}
                    onBlur={() => validate('fname', state.fname)}
                    errorMsg={error.fnameError}
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="lname"
                    label="Last Name"
                    maxLength="75"
                    placeholder="Enter last name"
                    value={state.lname}
                    onChange={changeHandler}
                    onBlur={() => validate('lname', state.lname)}
                    errorMsg={error.lnameError}
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="mobile"
                    label="Mobile Number"
                    maxLength="10"
                    placeholder="Enter Mobile number"
                    value={state.mobile}
                    onChange={changeHandler}
                    errorMsg={error.mobileError}
                    onBlur={() => validate('mobile', state.mobile)}
                />
            </div>


            <div className="col-md-12 text-right" style={{ marginTop: "15px" }}>
                <Button name="Submit" click={submit} />
            </div>

        </Aux>
    )

})

export default PersonalRef