import React, { memo } from 'react';
import './index.css';

const Button = memo((props) => {
    return (
        <button type="button"
            onClick={props.click}
            disabled={props.disabled !== undefined ? props.disabled : false}
            className={`btn_sub ${props.class !== undefined ? props.class : ""}`}>{props.name}</button>
    )
})

export default Button