import React, { useState, useEffect } from 'react';
import Aux from '../../../Atoms/children';
import Label from '../../../Atoms/label';
import Avtar from '../../../Atoms/Avatar';
import FMargin from '../../../Atoms/formMargin';
import { useDispatch } from 'react-redux';
import { employeeType } from '../../../../store/Action/PL/employeeAction';

import './index.css'
const Home = React.memo((props) => {
    const dispatch = useDispatch();
    const [emp, setEmp] = useState('SA')

    const changeHandler = (data) => {
        setEmp(data);
        dispatch(employeeType(data))
    }
    useEffect(() => {
        dispatch(employeeType('SA'))
    }, [])


    return (
        <Aux>
            <Label label="Select your employment type"></Label>
            <div className="col-md-12 padd0" style={{ marginTop: "10px" }}>
                <FMargin>
                    <div className={`col_Emp ${emp === "SA" ? "active" : ""}`} onClick={() => changeHandler("SA")}>
                        <span className="cirSpan"></span>
                        <div className="img_emp">
                            <Avtar image="salEmp.png" />
                        </div>
                        <div className="img_desc">
                            Salaried
                    </div>
                    </div>
                    <div className={`col_Emp ${emp === "SEP" ? "active" : ""}`} onClick={() => changeHandler("SEP")}>
                        <span className="cirSpan"></span>
                        <div className="img_emp">
                            <Avtar image="selfEmp.png" />
                        </div>
                        <div className="img_desc">
                            Self Employed
                    </div>
                    </div>
                </FMargin>
            </div>

        </Aux>
    )
})
export default Home