import * as type from '../../Action/PL/type';

let initialState = {
    emptype: "",
    masterData: "",
    bankName: [
        { label: "ICICI", value: "ICICI" },
        { label: "KOTAK", value: "KOTAK" },
        { label: "RBL", value: "RBL" },
        { label: "HDFC", value: "HDFC" },
        { label: "AXIS", value: "AXIS" },
        { label: "SBI", value: "SBI" },
        { label: "YES", value: "YES" },
        { label: "CITI", value: "CITI" },
        { label: "INDUSIND", value: "INDUSIND" },
        { label: "PNB", value: "PNB" },
        { label: "Others", value: "Others" },
    ],
    leadID: null
}



export default function employeeDetail(state = initialState, action) {
    let newState = { ...state };
    switch (action.type) {

        case type.Emp_Type:
            return {
                ...state,
                emptype: action.payload
            }
        case type.Emp_Master_SUCCESS:
            console.log(action.getval, 'success')
            return {
                ...state,
                masterData: action.getval
            }

        case type.Emp_Master_FAIL:

            return {
                ...state,
                masterData: ''
            }
        case type.GET_LEAD_ID:
            return {
                ...state,
                leadID: action.payload
            }
        default:
            return state
    }
}