import React, { Component } from 'react';
import { Switch, Route, withRouter } from 'react-router-dom';
import Home from '../../Organisms/personalLoans';
import BasicDetail from '../../Organisms/personalLoans/basicDetail';
import Offer from '../../Organisms/personalLoans/offer';
import BankData from '../../Organisms/personalLoans/bankData';
import { empMaster } from '../../../store/Action/PL/employeeAction';
import { connect } from 'react-redux';


class index extends Component {
    componentDidMount() {
        this.props.getmaster();
    }
    render() {
        return (
            <>
                <Switch>
                    <Route path="/pl/home" render={(value) => <Home {...value} />} />
                    <Route path="/pl/BasicDetail" render={(value) => <BasicDetail {...value} {...this.props} />} />
                    <Route path="/pl/Offers" render={(value) => <Offer {...value} {...this.props} />} />
                    <Route path="/pl/bankData" render={(value) => <BankData {...value} {...this.props} />} />
                </Switch>

            </>
        )
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        getmaster: (data) => dispatch(empMaster(data))
    }
}
const mapStateToProps = (state) => {
    return {
        emp: state.Emp
    }
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(index))
