import React, { useState } from 'react';
import Aux from '../../Atoms/children';
import Avatar from '../../Atoms/Avatar';
import Cart from '../../Atoms/cart';
import Button from '../../Atoms/button';
import RangeSlider from '../../Atoms/rangeSlider';
import { withRouter } from 'react-router-dom';
import './offercart.css';

const OfferCard = React.memo((props) => {
    const [state, setState] = useState(10);
    const onChange = (data) => {
        console.log(data, 'data')
        setState(data)
    }
    const submit = (bankName) => {
        props.history.push({ pathname: "/pl/bankData", state: { bank: bankName } })
    }
    // props.history.push("/personalloan/bankData");
    return (
        <Aux>
            {props.data.map((curr, index) => {
                return (
                    <Cart>
                        <div className="col-md-3">
                            <div class="img-thumbnail imgthumb">
                                <Avatar image="banklogo.webp" />
                            </div>
                        </div>
                        <div className="col-md-4">
                            <h2 className="mediaBank_name">{curr.bankName}</h2>
                            <div className="bank-sp">
                                <h6 >
                                    <span className="roi-lbl">ROI: </span> {curr.roi}%</h6>

                                <h6 className="proce-fee-desk">Processing Fee: <i className="fa fa-rupee"></i>&#x20B9; {curr.processingfee}</h6></div>

                            <div className="bank-sp">
                                <h6 >
                                    <span className="roi-lbl">EMI: </span><i className="fa fa-rupee"></i>&#x20B9; {curr.emi}</h6>
                                <h6 className="proce-fee-desk">Tenure: <span className="emi_tenor_0">{curr.tenure}</span>  months </h6></div>
                            <Button name="Apply" click={() => submit(curr.bankName)} class="sm_btn" />
                            <span className="bank-footer-details">Get money within 2 days*</span>
                        </div>
                        <div className="col-md-5 hide">
                            <div className="col-md-12 padd0 clearfix" style={{ marginTop: "5px" }}>
                                <div className="col-md-12 padd0" style={{ marginBottom: "0px" }}>
                                    <p className="loanDesc">Loan amount (in lakh)</p>
                                    <p className="loanDesc loanamount">&#x20B9; <string>0.32</string></p>
                                </div>
                                <div style={{ clear: "both" }}></div>
                                <RangeSlider
                                    min={0}
                                    max={100}
                                    onChange={onChange}
                                    value={state}
                                />
                            </div>
                            <div className="col-md-12 padd0 clearfix" style={{ marginTop: "0px" }}>
                                <div className="col-md-12 padd0" style={{ marginBottom: "0px" }}>
                                    <p className="loanDesc">Tenure (in months)</p>
                                    <p className="loanDesc loanamount"><strong>24</strong></p>
                                </div>
                                <div style={{ clear: "both" }}></div>
                                <RangeSlider />
                            </div>
                        </div>
                    </Cart>
                )
            })}



        </Aux >

    )
})
export default withRouter(OfferCard);