import React, { useState } from 'react';
import Container from '../../Atoms/container';
import Heading from '../../Atoms/heading';

import Avatar from '../../Atoms/Avatar';
import Paragra from '../../Atoms/paragraph';
import AddressDetail from '../addressDetail';
import BusinessDetail from './bank/businessDetail';
import PersonalDetail from './bank/personalDetail';
import PersonalRef from './bank/reference';
import { useSelector, useDispatch } from 'react-redux';
import PlApi from '../../../services/PLApi';
import './bank.css';
let api = new PlApi();

function BankData(props) {
    console.log(props, 'history')
    let bankName = ""
    let gethisData = props.history.location.state;
    if (gethisData !== undefined) {
        bankName = gethisData.bank
    }
    const getRedux = useSelector(state => state);
    console.log(getRedux, 'getRedux')
    let { Emp, login } = getRedux;
    const [address, setAddress] = useState(true);
    const [business, setBusiness] = useState(false);
    const [personal, setPersonal] = useState(true);
    const [ref, setRef] = useState(false);
    const [msg, setMsg] = useState(true);
    const [addressDetail, setAddressDetail] = useState({});
    // const [address, setAddress] = useState(true)
    const getPincode = (data) => {
        return api.getCityState(data).then(res => res)
    }
    const addressClick = (data) => {
        console.log(data, 'addressClick');
        setAddressDetail(data);
        setAddress(false);
        setBusiness(true);
        setPersonal(true)
    }
    const businessclick = (data) => {
        console.log(addressDetail, data, 'dddd')
        // { "emptype": Emp.emptype }
        let payload = { ...addressDetail, ...data, "emptype": Emp.emptype, "udid": login.uuid.uuid, "leadid": Emp.leadID, };
        api.businessDetails1(payload).then(res => {
            if (res.success) {
                setAddress(false);
                setBusiness(false);
                setPersonal(false);
                setRef(true)
            }
        })
        console.log(payload, 'payload');
    }
    const personalClick = (data) => {
        let paylaod = {
            ...addressDetail, ...data, "udid": login.uuid.uuid,
            "leadid": Emp.leadID
        }
        api.businessDetails2(paylaod).then(res => {
            if (res.success) {
                setAddress(false);
                setBusiness(false);
                setPersonal(false);
                setRef(true)
            }
        })
    }
    const refClick = (data) => {
        let payload = {
            ...data, "udid": login.uuid.uuid,
            "leadid": Emp.leadID
        }
        api.referenceDetails(payload).then(res => {
            if (res.success) {
                setAddress(false);
                setBusiness(false);
                setPersonal(false);
                setRef(false);
                setMsg(false);
            }
        })
    }
    return (
        <Container class="container-con padd_con">
            {msg ?
                <div className="col-md-12">
                    <Heading title="Congratulations!" class="head_mrg">
                        <Avatar image="clappingHands.webp" class="small_img" />
                    </Heading>
                    <Paragra para={`you have selected ${bankName} Bank jusy two steps away form submitting your Application !`} />


                </div> : <div className="col-md-12">
                    <Heading title="Thank you. We have received your application successfully!" class="head_mrg">
                        <Avatar image="clappingHands.webp" class="small_img" />
                    </Heading>
                    <Paragra style={{ marginLeft: "30px" }} para={`our executive will get in touch with you`} />


                </div>}

            {address ?
                < div className="col-md-12">
                    <Heading title="Residency Detail" class="bankTitle" />
                    <hr />
                    <AddressDetail {...props} addressClick={addressClick} pincodeFn={getPincode} />
                </div> : null
            }
            {business ?
                <React.Fragment>
                    {
                        Emp.emptype === "SEP" ?
                            <div className="col-md-12">
                                <Heading title="Business Detail" class="bankTitle" />
                                <hr />
                                <BusinessDetail {...props} businessClick={businessclick} pincodeFn={getPincode} />
                            </div> : null
                    }
                </React.Fragment> : null
            }

            {personal ?
                <React.Fragment>
                    {
                        Emp.emptype === "SA" ?
                            <div className="col-md-12">
                                <Heading title="Personal Details" class="bankTitle" />
                                <hr />
                                <PersonalDetail pincodeFn={getPincode} {...props} personalClick={personalClick} />
                            </div> : null
                    }
                </React.Fragment> : null}

            {ref ?
                <div className="col-md-12">
                    <Heading title="Personal Reference" class="bankTitle" />
                    <hr />
                    <PersonalRef pincodeFn={getPincode} {...props} refClick={refClick} />
                </div> : null}



            {/* <div className="col-md-12 padd0">
                <div className="col-md-4">
                    <Input
                        placeholder="Enter first name"
                        label="First Name"
                        type="text"
                        name="Fname"

                    />
                </div>
                <div className="col-md-4">
                    <Input
                        placeholder="Enter middle number"
                        label="Middle Name"
                        type="text"
                        name="Mname"

                    />
                </div>
                <div className="col-md-4">
                    <Input
                        placeholder="Enter last name"
                        label="Last Name"
                        type="text"
                        name="Lname"

                    />
                </div>
                <div className="col-md-4">
                    <Input
                        placeholder="Enter DOB"
                        label="Date of Birth"
                        type="text"
                        name="DOB"

                    />
                </div>
                <div className="col-md-4">
                    <Input
                        placeholder="Enter pincode"
                        label="Pincode"
                        type="text"
                        name="pincode"

                    />
                </div>
                <div className="col-md-4">
                    <Input
                        placeholder="Enter PAN No"
                        label="What's your PAN number?"
                        type="text"
                        name="panNO"

                    />
                </div>
            </div>

            <div className="col-md-12 text-right" style={{ marginTop: "15px" }}>
                <Button name="Validate PAN" />
            </div> */}




        </Container >
    )
}

export default React.memo(BankData)
