import { delay, takeLatest, call } from 'redux-saga/effects';
import * as type from '../../store/Action/PL/type';
import { takeEvery, put, fork } from 'redux-saga/effects';
import PLApi from '../../services/PLApi';
let apiPL = new PLApi()

async function fetchData() {
    const rawResponse = await apiPL.getMasters();
    return rawResponse
}
function* getEmp() {
    console.log('Sujeet')
    let data = yield call(fetchData);
    //let data = apiPL.getMasters();
    if (data.success) {
        let getval = data.data;
        yield put({ type: type.Emp_Master_SUCCESS, getval })
    } else {
        let emp = "";
        yield put({ type: type.Emp_Master_FAIL, emp })
    }
    console.log('sujeet', data)
}

export function* empMaster() {
    yield takeLatest(type.Emp_Master, getEmp)
}