import React, { memo, useState } from 'react';
import Aux from '../../../Atoms/children';
import Input from '../../../Molecules/input';
import SelectBox from '../../../Molecules/select';
import Button from '../../../Molecules/button';
import { numberText, nameText, dropvali, panValidation, nameValid, emailValidator } from '../../../../helper/method';

const PersonalDetail = memo(props => {
    let maritalStatus = [
        { label: "Single", value: "SI" },
        { label: "Married", value: "MA" },
        { label: "widow/widower", value: "WI" },
        { label: "Divorce", value: "DI" }
    ]
    const [error, setError] = useState({});
    const [state, setState] = useState({ fname: "", lName: "", gender: "", maritalStatus: "", highestQuli: "", noOfdep: "", currResi: "", currResidence: "", pan: "", purposeLoan: "", qualification: "", email: "" });
    const changeHandler = (name, value) => {
        let allState = { ...state };
        if (name === "fname" || name === "lName") {
            if (value === "" || nameText(value)) {
                allState[name] = value;
            }
        } else if (name === 'noOfdep' || name === "currResi" || name === "currResidence") {
            if (value === "" || numberText(value)) {
                allState[name] = value;
            }
        } else {
            allState[name] = value;
        }
        setState(allState)
    }
    const validate = (name, value) => {
        let type = name;
        let errorMsg = { ...error };
        if (type === "fname") {
            let check = nameValid(value);
            if (!check.res) {
                errorMsg['fnameError'] = check.msg
            } else {
                errorMsg['fnameError'] = check.msg
            }
            setError(errorMsg);
        }
        if (type === "lName") {
            let check = nameValid(value);
            if (!check.res) {
                errorMsg['lNameError'] = check.msg
            } else {
                errorMsg['lNameError'] = check.msg
            }
            setError(errorMsg);
        }
        if (type === "gender") {
            let check = dropvali(value);
            if (!check.res) {
                errorMsg['genderError'] = check.msg
            } else {
                errorMsg['genderError'] = check.msg
            }
            setError(errorMsg);
        }
        if (type === "maritalStatus") {
            let check = dropvali(value);
            if (!check.res) {
                errorMsg['maritalStatusError'] = check.msg
            } else {
                errorMsg['maritalStatusError'] = check.msg
            }
            setError(errorMsg);
        }
        if (type === "highestQuli") {
            let check = dropvali(value);
            if (!check.res) {
                errorMsg['highestQuliError'] = check.msg
            } else {
                errorMsg['highestQuliError'] = check.msg
            }
            setError(errorMsg);
        }
        if (type === "noOfdep") {
            let check = dropvali(value);
            if (value === "") {
                errorMsg['noOfdepError'] = "Please enter no of dependent"
            } else {
                errorMsg['noOfdepError'] = ""
            }
            setError(errorMsg);
        }
        if (type === "currResi") {
            let check = dropvali(value);
            if (value === "") {
                errorMsg['currResiError'] = "Please enter current residence year"
            } else {
                errorMsg['currResiError'] = ""
            }
            setError(errorMsg);
        }
        if (type === "currResidence") {
            let check = dropvali(value);
            if (value === "") {
                errorMsg['currResidenceError'] = "Please enter current  year"
            } else {
                errorMsg['currResidenceError'] = ""
            }
            setError(errorMsg);
        }
        if (type === "pan") {
            let check = panValidation(value);
            if (!check.res) {
                errorMsg['panError'] = check.msg
            } else {
                errorMsg['panError'] = check.msg
            }
            setError(errorMsg);
        }
        if (type === "purposeLoan") {
            let check = dropvali(value);
            if (!check.res) {
                errorMsg['purposeLoanError'] = check.msg
            } else {
                errorMsg['purposeLoanError'] = check.msg
            }
            setError(errorMsg);
        }
        if (type === "qualification") {
            let check = dropvali(value);
            if (!check.res) {
                errorMsg['qualificationError'] = check.msg
            } else {
                errorMsg['qualificationError'] = check.msg
            }
            setError(errorMsg);
        }
        if (type === "email") {
            let check = emailValidator(value);
            if (!check.res) {
                errorMsg['emailError'] = check.msg
            } else {
                errorMsg['emailError'] = check.msg
            }
            setError(errorMsg);
        }
    }

    const submitValid = () => {
        let isValid = false;
        let errorMsg = { ...error };
        let { fname, lName, gender, maritalStatus, highestQuli, noOfdep, currResi, currResidence, pan, purposeLoan, qualification, email } = state;
        if (!nameValid(fname).res) {
            errorMsg['fnameError'] = nameValid(fname).msg
        } else if (!nameValid(lName).res) {
            errorMsg['lNameError'] = nameValid(lName).msg
        } else if (!dropvali(gender).res) {
            errorMsg['genderError'] = dropvali(gender).msg
        } else if (!dropvali(maritalStatus).res) {
            errorMsg['maritalStatusError'] = dropvali(maritalStatus).msg
        } else if (!dropvali(highestQuli).res) {
            errorMsg['highestQuliError'] = dropvali(highestQuli).msg
        } else if (noOfdep === "") {
            errorMsg['noOfdepError'] = "Please enter no of dependent"
        } else if (currResi === "") {
            errorMsg['currResiError'] = "Please enter current residence year"
        } else if (currResidence === "") {
            errorMsg['currResidenceError'] = "Please enter current  year"
        } else if (!panValidation(pan).res) {
            errorMsg['panError'] = panValidation(pan).msg
        } else if (!dropvali(purposeLoan).res) {
            errorMsg['purposeLoanError'] = dropvali(purposeLoan).msg
        } else if (!dropvali(qualification).res) {
            errorMsg['qualificationError'] = dropvali(qualification).msg
        } else if (!emailValidator(email).res) {
            errorMsg['emailError'] = emailValidator(email).msg
        } else {
            errorMsg['fnameError'] = "";
            errorMsg['lNameError'] = "";
            errorMsg['genderError'] = "";
            errorMsg['maritalStatusError'] = "";
            errorMsg['highestQuliError'] = "";
            errorMsg['noOfdepError'] = "";
            errorMsg['currResiError'] = "";
            errorMsg['currResidenceError'] = "";
            errorMsg['panError'] = "";
            errorMsg['purposeLoanError'] = "";
            errorMsg['qualificationError'] = "";
            errorMsg['emailError'] = "";
            isValid = true
        }

        setError(errorMsg);
        return isValid
    }
    const submit = (data) => {
        let check = submitValid();
        if (check) {
            let { fname, lName, gender, maritalStatus, highestQuli, noOfdep, currResi, currResidence, pan, purposeLoan, qualification, email } = state;
            let layout = {

                "fname": fname,
                "lname": lName,
                "gender": gender,
                "maritalstatus": maritalStatus,
                "highestqualification": highestQuli,
                "noofdependent": noOfdep,
                "currresisince": currResi,
                "citysince": currResidence,
                "pan": pan,
                "loanpurpose": purposeLoan,
                "qualification": qualification,

            }
            props.personalClick(layout);
        }

    }

    let { emp: { masterData } } = props;
    console.log(props, 'allprops', masterData)
    let { Qualification, HighestQualification, Gender, LoanPurpose } = masterData
    return (
        <Aux>
            <div className="col-md-4">
                <Input
                    name="fname"
                    label="First Name"
                    placeholder="Enter first name"
                    value={state.fname}
                    onChange={changeHandler}
                    onBlur={() => validate('fname', state.fname)}
                    errorMsg={error.fnameError}
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="lName"
                    label="Last Name"
                    placeholder="Enter last name"
                    value={state.lName}
                    onChange={changeHandler}
                    onBlur={() => validate('lName', state.lName)}
                    errorMsg={error.lNameError}
                />
            </div>

            <div className="col-md-4">
                <SelectBox
                    name="gender"
                    label="Gender"
                    options={Gender}
                    placeholder="Select gender"
                    value={state.gender}
                    onChange={changeHandler}
                    onBlur={() => validate('gender', state.gender)}
                    errorMsg={error.genderError}
                />
            </div>
            <div className="col-md-4">
                <SelectBox
                    name="maritalStatus"
                    label="Marital Status"
                    options={maritalStatus}
                    placeholder="Select marital status"
                    value={state.maritalStatus}
                    onChange={changeHandler}
                    onBlur={() => validate('maritalStatus', state.maritalStatus)}
                    errorMsg={error.maritalStatusError}
                />
            </div>
            <div className="col-md-4">
                <SelectBox
                    name="highestQuli"
                    label="Highest Qualification"
                    options={HighestQualification}
                    placeholder="Select qualification"
                    value={state.highestQuli}
                    onChange={changeHandler}
                    onBlur={() => validate('highestQuli', state.highestQuli)}
                    errorMsg={error.highestQuliError}
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="noOfdep"
                    label="No of dependent"
                    maxLength="2"
                    placeholder="Enter no of dependent"
                    value={state.noOfdep}
                    onChange={changeHandler}
                    onBlur={() => validate('noOfdep', state.noOfdep)}
                    errorMsg={error.noOfdepError}
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="currResi"
                    maxLength="3"
                    placeholder="Enter staying in current resedence since"
                    label="staying in current residence since (in year)"
                    value={state.currResi}
                    onChange={changeHandler}
                    onBlur={() => validate('currResi', state.currResi)}
                    errorMsg={error.currResiError}

                />
            </div>
            <div className="col-md-4">
                <Input
                    name="currResidence"
                    maxLength="3"
                    placeholder="Enter staying in city  since"
                    label="staying in  city  since (in year)"
                    value={state.currResidence}
                    onChange={changeHandler}
                    onBlur={() => validate('currResidence', state.currResidence)}
                    errorMsg={error.currResidenceError}

                />
            </div>
            <div className="col-md-4">
                <Input
                    name="pan"
                    label="Personal PAN"
                    placeholder="Enter PAN NO"
                    value={state.pan}
                    onChange={changeHandler}
                    onBlur={() => validate('pan', state.pan)}
                    errorMsg={error.panError}

                />
            </div>
            <div className="col-md-4">
                <SelectBox
                    name="purposeLoan"
                    label="Purpose of loan"
                    options={LoanPurpose}
                    placeholder="Select purpose loan"
                    value={state.LoanPurpose}
                    onChange={changeHandler}
                    onBlur={() => validate('LoanPurpose', state.purposeLoan)}
                    errorMsg={error.purposeLoanError}
                />
            </div>
            <div className="col-md-4">
                <SelectBox
                    name="qualification"
                    label="Qulification"
                    options={Qualification}
                    placeholder="Select qualification"
                    value={state.qualification}
                    onChange={changeHandler}
                    onBlur={() => validate('qualification', state.qualification)}
                    errorMsg={error.qualificationError}
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="email"
                    label="Email Address"
                    placeholder="Enter email address"
                    value={state.email}
                    onChange={changeHandler}
                    onBlur={() => validate('email', state.email)}
                    errorMsg={error.emailError}
                />
            </div>
            <div className="col-md-12 text-right" style={{ marginTop: "15px" }}>
                <Button name="Submit" click={submit} />
            </div>


        </Aux>
    )

})

export default PersonalDetail