import React, { memo, useState, useEffect } from 'react';
import Aux from '../../../Atoms/children';
import Input from '../../../Molecules/input';
import SelectBox from '../../../Molecules/select';
import Button from '../../../Molecules/button';
import { numberText, addressVali, comNameValue, pincodeVali, dropvali } from '../../../../helper/method';
import { otpValidation } from '../../../../helper/method';

const BusinesDetail = memo(props => {
    console.log(props, 'businesspart')
    let gender = [
        { label: "male", value: "male" },
        { label: "Femal", value: "Femal" },
        { label: "Other", value: "Other" }
    ]
    let mailOpation = [
        { label: "CA", value: "CA" },
        { label: "OA", value: "OA" }
    ];
    const [error, setError] = useState({});
    const [pinstatus, setPincode] = useState(true);
    const [state, setState] = useState({ employerName: "", addressLine1: "", addressLine2: "", pincode: "", state: "", city: "", mail: "" });

    const validate = (name, value) => {
        let type = name;
        let errorMsg = { ...error };
        if (type === "employerName") {
            let check = comNameValue(value);
            if (!check.res) {
                errorMsg['employerNameError'] = check.msg
            } else {
                errorMsg['employerNameError'] = check.msg
            }
            setError(errorMsg)
        }
        if (type === "addressLine1") {
            let check = addressVali(value);
            if (!check.res) {
                errorMsg['addressLine1Error'] = check.msg
            } else {
                errorMsg['addressLine1Error'] = check.msg
            }
            setError(errorMsg)
        }
        if (type === "addressLine2") {
            let check = addressVali(value);
            if (!check.res) {
                errorMsg['addressLine2Error'] = check.msg
            } else {
                errorMsg['addressLine2Error'] = check.msg
            }
            setError(errorMsg)
        }
        if (type === "pincode") {
            let check = pincodeVali(value);
            if (!check.res) {
                errorMsg['pincodeError'] = check.msg
            } else {
                // errorMsg['pincodeError'] = check.msg;
                let payload = {
                    "pincode": value
                }
                props.pincodeFn(payload).then(res => {
                    console.log(res, 'pincode')
                    if (res.success) {
                        errorMsg['pincodeError'] = "";
                        let data = res.response[0];
                        setState(prevState => ({
                            ...prevState,
                            city: data.cityname,
                            state: data.statename,
                        }))
                        setPincode(false)
                    } else {
                        errorMsg['pincodeError'] = res.response;
                    }
                    setError(errorMsg)
                })

            }
            //setError(errorMsg)
        }
        if (type === "mail") {
            let check = dropvali(value);
            if (!check.res) {
                errorMsg['mailError'] = check.msg
            } else {
                errorMsg['mailError'] = check.msg
            }
            setError(errorMsg)
        }
    }

    const changeHandler = (name, value) => {
        let allState = { ...state };
        if (name === "pincode") {
            if (value === "" || numberText(value)) {
                allState[name] = value;
            }
        } else {
            allState[name] = value;
        }
        setState(allState)
    }
    const submitValidation = () => {
        let errorMsg = { ...error }
        let { employerName, addressLine1, addressLine2, pincode, city, mail } = state;
        let isValid = false;
        if (!comNameValue(employerName).res) {
            errorMsg['employerNameError'] = comNameValue(employerName).msg
        } else if (!addressVali(addressLine1).res) {
            errorMsg['addressLine1Error'] = addressVali(addressLine1).msg
        } else if (!addressVali(addressLine2).res) {
            errorMsg['addressLine2Error'] = addressVali(addressLine2).msg
        } else if (!pincodeVali(pincode)) {
            errorMsg['pincodeError'] = addressVali(pincode).msg
        } else if (!dropvali(mail).res) {
            errorMsg['mailError'] = dropvali(mail).msg
        } else {
            errorMsg['employerNameError'] = "";
            errorMsg['addressLine1Error'] = "";
            errorMsg['addressLine2Error'] = "";
            errorMsg['pincodeError'] = "";
            errorMsg['mailError'] = "";
            isValid = true
        }
        setError(errorMsg);
        return isValid;

    }

    const submit = () => {
        let check = submitValidation();
        if (check) {
            let { employerName, addressLine1, addressLine2, pincode, city, mail } = state;
            let paylaod = {

                "employerName": employerName,
                "bAddLine1": addressLine1,
                "bAddLine2": addressLine2,
                "offState": state.state,
                "offCity": city,
                "offPincode": pincode,
                "mailingAdd": mail,
                "gstNum": "",

            }
            props.businessClick(paylaod)
        }

    }
    console.log(state, 'allState')
    return (
        <Aux>
            <div className="col-md-4">
                <Input
                    name="employerName"
                    label="Employer Name"
                    placeholder="Enter Enployer Name"
                    onChange={changeHandler}
                    value={state.employerName}
                    onBlur={() => validate('employerName', state.employerName)}
                    errorMsg={error.employerNameError}
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="addressLine1"
                    label="Address Line"
                    value={state.addressLine1}
                    placeholder="Enter Address1"
                    onChange={changeHandler}
                    maxLength="60"
                    onBlur={() => validate('addressLine1', state.addressLine1)}
                    errorMsg={error.addressLine1Error}
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="addressLine2"
                    label="Address Line 2"
                    value={state.addressLine2}
                    placeholder="Enter Address2"
                    onChange={changeHandler}
                    maxLength="60"
                    onBlur={() => validate('addressLine2', state.addressLine2)}
                    errorMsg={error.addressLine2Error}
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="pincode"
                    label="Office pincode"
                    value={state.pincode}
                    placeholder="Enter pincode"
                    maxLength="6"
                    onChange={changeHandler}
                    onBlur={() => validate('pincode', state.pincode)}
                    errorMsg={error.pincodeError}
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="state"
                    label="Office State"
                    value={state.state}
                    disabled={true}
                    placeholder="Enter office State"
                />
            </div>
            <div className="col-md-4">
                <Input
                    name="city"
                    label="Office City"
                    value={state.city}
                    disabled={true}
                    placeholder="Enter office City"
                />
            </div>

            <div className="col-md-4">
                <SelectBox
                    name="mail"
                    label="Mailing Address"
                    options={mailOpation}
                    value={state.mail}
                    placeholder="Select mailing address"
                    onChange={changeHandler}
                    onBlur={() => validate('mail', state.mail)}
                    errorMsg={error.mailError}
                />
            </div>
            <div className="col-md-12 text-right" style={{ marginTop: "15px" }}>
                <Button name="Submit" disabled={pinstatus} click={submit} />
            </div>

        </Aux>
    )

})

export default BusinesDetail