import Api from '.';
import { endpoints } from './endPoint';

export default class LoginApi extends Api {

    userExist(data) {
        let payload = {
            "api_name": "primaryData",
            "method": "post",
            "request": {
                "data": data
            }
        };
        let url = this.buildUrl(endpoints.login.userExist)
        return this.fetch(url, "POST", JSON.stringify(payload)).then(response => response);
    }
    getUserData(data) {
        let layout = {
            "api_name": "primaryData",
            "method": "post",
            "request": {
                "data": data
            }
        }
        let url = this.buildUrl(endpoints.login.getUserData)
        return this.fetch(url, "POST", JSON.stringify(layout)).then(response => response);
    }
    sendOtp(data) {
        let layout = {
            "api_name": "sendOtp",
            "method": "post",
            "request": {
                "data": data
            }
        }
        let url = this.buildUrl(endpoints.login.sendOtp)
        return this.fetch(url, "POST", JSON.stringify(layout)).then(response => response);
    }
    fastCheck(data) {
        let layout = {
            "api_name": "fastCheck",
            "method": "post",
            "request": {
                "data": data
            }
        }
        let url = this.buildUrl(endpoints.login.fastCheck)
        return this.fetch(url, "POST", JSON.stringify(layout)).then(response => response);
    }

}