import loginApi from '../services/loginApi';
import { delay, takeLatest, call, put, takeEvery } from 'redux-saga/effects';
import * as type from '../store/Action/type';
import { getUserData } from '../store/Action/loginAction';
let api = new loginApi()

async function fetchata(res) {
    let data = await api.getUserData(res);
    return data;
}

function* calluser(action) {
    let payload = action.payload;
    let apiData = yield call(fetchata, payload);
    if (apiData.success) {
        let res = apiData.response.userData;
        console.log(res, 'saga')
        yield put({ type: type.GET_USER_SUCCESS, payload: res })
    } else {
        let res = null;
        yield put({ type: type.GET_USER_FAIL, payload: res })
    }
    console.log(apiData, 'callUser')
}


export function* GetUserData() {
    yield takeEvery(type.GET_USER_DATA, calluser)
}
