import React, { memo } from 'react';
import FMargin from '../../Atoms/formMargin';
import Label from '../../Atoms/label';
import Select from '../../Atoms/select';
import Error from '../../Atoms/error';

const Selectbox = memo((props) => {
    const change = (e) => {
        // console.log(e.value, 'select')
        props.onChange(props.name, e.value)
    }
    return (
        <FMargin>
            <Label label={props.label} />
            <Select
                placeholder={props.placeholder}
                value={props.options.find(curr => curr.value === props.value ? props.value : "")}
                options={props.options}
                onBlur={props.onBlur}
                disabled={props.disabled}
                name={props.name}
                onChange={change}
            />
            <Error errorMsg={props.errorMsg} />
        </FMargin>
    )
})

export default Selectbox