import React, { useState } from 'react';
import Aux from '../../Atoms/children';
import Select from '../../Molecules/select';
import Input from '../../Molecules/input';
import Button from '../../Molecules/button';
import { useSelector } from 'react-redux';
import { numberText, annualvali, loanvali, dropvali, emivali, comNameValue } from '../../../helper/method';

const SelfempDetail = React.memo((props) => {
    const getRedux = useSelector(state => state);
    // let gender = [
    //     { label: "male", value: "male" },
    //     { label: "Femal", value: "Femal" },
    //     { label: "Other", value: "Other" }
    // ]

    const [state, setState] = useState({ gender: "", annualIncome: "", loanPurpose: "", emi: "", profession: "", workEx: "", bank: "", resiType: "", city: props.city, loanAmt: "" })
    const [error, setError] = useState({});
    const validate = (name, value) => {
        let type = name;
        let errorMsg = { ...error };
        if (type === "gender") {
            let genderV = dropvali(value);
            if (!genderV.res) {
                errorMsg['genderError'] = genderV.msg;
            } else {
                errorMsg['genderError'] = genderV.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "annualIncome") {
            let anuuCheck = annualvali(value);
            if (!anuuCheck.res) {
                errorMsg['annualIncomeError'] = anuuCheck.msg;
            } else {
                errorMsg['annualIncomeError'] = anuuCheck.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "loanPurpose") {
            let CheckValue = dropvali(value);
            if (!CheckValue.res) {
                errorMsg['loanPurposeError'] = CheckValue.msg;
            } else {
                errorMsg['loanPurposeError'] = CheckValue.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "loanPurpose") {
            let CheckValue = dropvali(value);
            if (!CheckValue.res) {
                errorMsg['loanPurposeError'] = CheckValue.msg;
            } else {
                errorMsg['loanPurposeError'] = CheckValue.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "emi") {
            let emCheck = emivali(value);
            if (!emCheck.res) {
                errorMsg['emiError'] = emCheck.msg;
            } else {
                errorMsg['emiError'] = emCheck.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "profession") {
            let CheckValue = dropvali(value);
            if (!CheckValue.res) {
                errorMsg['professionError'] = CheckValue.msg;
            } else {
                errorMsg['professionError'] = CheckValue.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "workEx") {
            let CheckValue = dropvali(value);
            if (!CheckValue.res) {
                errorMsg['workExError'] = CheckValue.msg;
            } else {
                errorMsg['workExError'] = CheckValue.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "bank") {
            let CheckValue = dropvali(value);
            if (!CheckValue.res) {
                errorMsg['bankError'] = CheckValue.msg;
            } else {
                errorMsg['bankError'] = CheckValue.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === 'resiType') {
            let CheckValue = dropvali(value);
            if (!CheckValue.res) {
                errorMsg['resiTypeError'] = CheckValue.msg;
            } else {
                errorMsg['resiTypeError'] = CheckValue.msg;
            }
            setError({ ...errorMsg });
        }
        if (type === "loanAmt") {
            let amtcheck = loanvali(value);
            if (!amtcheck.res) {
                errorMsg['loanAmtError'] = amtcheck.msg;
            } else {
                errorMsg['loanAmtError'] = amtcheck.msg;
            }
            setError({ ...errorMsg });
        }

    }
    const changeHandler = (name, value) => {
        let allState = { ...state }
        if (name === "loanAmt" || name === "emi" || name === "annualIncome") {
            if (value === "" || numberText(value)) {
                allState[name] = value
            }
        } else {
            allState[name] = value
        }
        setState(allState)
    }
    // console.log(props, 'allprops')
    const formValid = () => {
        let { gender, annualIncome, loanPurpose, emi, profession, workEx, bank, resiType, city, loanAmt } = state;
        let errorMsg = { ...error };
        let formValid = false;
        let genderCheck = dropvali(gender);
        if (!genderCheck.res) {
            errorMsg['genderError'] = genderCheck.msg;
        } else if (!annualvali(annualIncome).res) {
            errorMsg['annualIncomeError'] = annualvali(annualIncome).msg;
        } else if (!dropvali(loanPurpose).res) {
            errorMsg['loanPurposeError'] = dropvali(loanPurpose).msg;
        } else if (!emivali(emi).res) {
            errorMsg['emiError'] = emivali(emi).msg;
        } else if (!dropvali(profession).res) {
            errorMsg['professionError'] = dropvali(profession).msg;
        } else if (!dropvali(workEx).res) {
            errorMsg['workExError'] = dropvali(workEx).msg;
        } else if (!dropvali(bank).res) {
            errorMsg['bankError'] = dropvali(bank).msg;
        } else if (!dropvali(resiType).res) {
            errorMsg['resiTypeError'] = dropvali(resiType).msg;
        }
        else if (!loanvali(loanAmt).res) {
            errorMsg['loanAmtError'] = loanvali(loanAmt).msg;
        } else {
            errorMsg['genderError'] = "";
            formValid = true
        }
        setError({ ...errorMsg });
        return formValid
    }
    const submit = () => {
        let { gender, annualIncome, loanPurpose, emi, profession, workEx, bank, resiType, city, loanAmt } = state;
        let checkValid = formValid();
        if (checkValid) {
            let data = {
                "udid": getRedux.login.uuid.uuid,
                "leadid": getRedux.Emp.leadID,
                "gender": gender,
                "monthlyIncome": "0",
                "grossAnnualInc": annualIncome,
                "loanPurpose": loanPurpose,
                "totalEmi": emi,
                "employerName": "",
                "currWorkExp": workEx,
                "totalWorkExp": "",
                "resCity": city,
                "resType": resiType,
                "salaryBank": "",
                "moratoriumAvail": false,
                "profType": profession,
                "currProfExp": "0",
                "savingAcc": bank,
                "emptype": getRedux.Emp.emptype,
                "reqLoanAmt": loanAmt
            }
            props.finalSubmit(data)
        }
    }
    let bankname = props.emp.bankName;
    let { LoanPurpose, ResiType, Profession, WorkExp, BussWorkExp, Gender } = props.dropdown
    return (
        <Aux>
            <div className="col-md-4">
                <Select
                    label="Select Gender"
                    name="gender"
                    value={state.gender}
                    options={Gender}
                    onBlur={() => validate('gender', state.gender)}
                    onChange={changeHandler}
                    errorMsg={error.genderError}
                />
            </div>

            <div className="col-md-4">
                <Input
                    label="Your gross annual income"
                    value={state.annualIncome}
                    onChange={changeHandler}
                    placeholder="Enter gross income"
                    name="annualIncome"
                    onBlur={() => validate('annualIncome', state.annualIncome)}
                    errorMsg={error.annualIncomeError}
                    value={state.annualIncome}
                />
            </div>
            <div className="col-md-4">
                <Select
                    label="What is the purpose of your loan"
                    name="loanPurpose"
                    options={LoanPurpose}
                    onBlur={() => validate('loanPurpose', state.loanPurpose)}
                    onChange={changeHandler}
                    errorMsg={error.loanPurposeError}
                    value={state.loanPurpose}
                />
            </div>
            <div className="col-md-4">
                <Input
                    placeholder="Enter your current EMI"
                    label="Total EMI you pay currently"
                    name="emi"
                    onBlur={() => validate('emi', state.emi)}
                    onChange={changeHandler}
                    errorMsg={error.emiError}
                    value={state.emi}
                />
            </div>
            <div className="col-md-4">
                <Select
                    placeholder="Select your profession"
                    label="Select profession type"
                    name="profession"
                    options={Profession}
                    onBlur={() => validate('profession', state.profession)}
                    onChange={changeHandler}
                    errorMsg={error.professionError}
                    value={state.profession}

                />
            </div>
            <div className="col-md-4">
                <Select
                    placeholder="Select current profession"
                    label="Year in current profession"
                    options={BussWorkExp}
                    name="workEx"
                    onBlur={() => validate('workEx', state.workEx)}
                    onChange={changeHandler}
                    errorMsg={error.workExError}
                    value={state.workEx}
                />
            </div>
            <div className="col-md-4">
                <Select
                    placeholder="Select primary bank"
                    label="Your primary bank account is with"
                    name="bank"
                    options={bankname}
                    onBlur={() => validate('bank', state.bank)}
                    onChange={changeHandler}
                    errorMsg={error.bankError}
                    value={state.bank}
                />
            </div>
            <div className="col-md-4">
                <Select
                    placeholder="Select Residence type"
                    label="Residence type"
                    options={ResiType}
                    name="resiType"
                    onBlur={() => validate('resiType', state.resiType)}
                    onChange={changeHandler}
                    errorMsg={error.resiTypeError}
                    value={state.resiType}
                />
            </div>
            <div className="col-md-4">
                <Input
                    placeholder="Select Residence city"
                    label="Your Current city in Residence"
                    name="city"
                    disabled={true}
                    value={props.city}
                //options={gender}
                />
            </div>
            <div className="col-md-4">
                <Input
                    placeholder="Enter loan amount required"
                    label="Enter loan amount required"
                    name="loanAmt"
                    onBlur={() => validate('loanAmt', state.loanAmt)}
                    onChange={changeHandler}
                    errorMsg={error.loanAmtError}
                    value={state.loanAmt}
                />
            </div>
            <div className="col-md-12" style={{ marginTop: "15px" }}>
                <Button name="Show Offers" click={submit} />
            </div>
        </Aux>

    )
})

export default SelfempDetail