export const validateDOB = (dob) => {
    var today = new Date();
    let currDate = today.getDate();
    let currMonth = today.getMonth();
    let date = dob;
    let splittedDate = date.split("/");

    if (dob === "") {
        return { success: false, msg: Msg.PERSONAL_PAGE.BLANK_DOB };
    } else if (splittedDate.length === 3) {
        // valid age
        if (
            splittedDate[0].length === 2 &&
            splittedDate[1].length === 2 &&
            splittedDate[2].length === 4
        ) {
            //console.log(splittedDate[0],"==",splittedDate[1],"==",splittedDate[2])

            let days = daysInMonth(splittedDate[1], splittedDate[2]);
            console.log(days, "days", splittedDate[0]);
            if (splittedDate[0] <= days) {
                return ageCalculate(dob);
            } else {
                return { res: false, msg: Msg.PERSONAL_PAGE.VALID_DOB };
            }
        } else {
            return { res: false, msg: Msg.PERSONAL_PAGE.VALID_DOB };
        }
    } else {
        //invalid age
        return { res: false, msg: Msg.PERSONAL_PAGE.VALID_DOB };
    }
};
export const ageCalculate = (dob) => {
    var txtNomDOB = dob.split("/");
    var dateString = txtNomDOB[2] + "/" + txtNomDOB[1] + "/" + txtNomDOB[0];
    var today = new Date();
    var birthDate = new Date(dateString);
    var age = today.getFullYear() - birthDate.getFullYear();
    var m = today.getMonth() - birthDate.getMonth();
    if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
        age--;
    }
    return { res: true, msg: "", age: age };
};
function daysInMonth(month, year) {
    return new Date(year, month, 0).getDate();
}

export const pincodeVali = (data) => {
    let isValid = true;
    let msg = "";
    if (!data) {
        msg = Msg.ADDRESS_PAGE.BLANK_PINCODE;
        isValid = false;
    } else if (data.length !== 6) {
        msg = Msg.ADDRESS_PAGE.VALID_PINCODE;
        isValid = false;
    } else if (data <= 0) {
        msg = Msg.ADDRESS_PAGE.VALID_PINCODE;
        isValid = false;
    } else {
        msg = "";
        isValid = true;
    }
    return {
        res: isValid,
        msg: msg,
    };
};
export const panValidation = (panNumber) => {
    let isValid = true;
    let msg = "";
    if (!panNumber) {
        msg = Msg.PAN_NUMBER.BLANK_PAN;
        isValid = false;
    } else if (
        /^([a-zA-Z]){5}([0-9]){4}([a-zA-Z]){1}?$/.test(panNumber) === false
    ) {
        msg = Msg.PAN_NUMBER.VALID_PAN;
        isValid = false;
    } else {
        msg = "";
        isValid = true;
    }
    return {
        res: isValid,
        msg: msg,
    };
};

